import React from 'react';
import s from './PersonDetails.module.scss';
import ItemPerson from './ItemPerson';
import SwapiService from '../../services/swapi-service';
import Preloader from '../Preloader/Preloader';
import Error from '../Error/Error';
import {withRouter} from 'react-router-dom';
const PersonDetails = (props)=>{
    let {person:{name,gender,birth_year,eye_color}} = props;
    return (
        <div key={`${props.match.params.id}`} >
            <div> <img className={s.img} alt="person" src={`https://starwars-visualguide.com/assets/img/characters/${props.match.params.id}.jpg`}></img>  </div>
            <div>
                <h2 className="display-7">{name}</h2>
                <span className={`lead ${s.text}`}>Gender {gender}</span>
                <hr className={`my-4 ${s.hr}`}></hr>
                <span className={`lead ${s.text}`}>Birth Year {birth_year}</span>
                <hr className={`my-4 ${s.hr}`}></hr>
                <span className={`lead ${s.text}`}>Eye color {eye_color}</span>
            </div>
        </div>
    )
}
let PersonDetailsRouter = withRouter(PersonDetails);
class PersonDetailsContainers extends React.Component{
    swapiServise = new SwapiService();
    state = {
        allPeople: null,
        person: null,
        preload: false
    }
    componentDidMount(){
        this.getAllPeople();

    }
    getAllPeople =()=>{
        this.swapiServise.getAllPeople().then((res)=>{
            this.setState((state)=>{
                return{
                    ...state,
                    allPeople: res.results
                }
            })
        }).catch(this.newError())
    }
    newError = ()=>{
        return <Error/>
    }
    renderItemList = ()=>{
        if(this.state.allPeople){
         return   this.state.allPeople.map((e)=>{
                         return <ul key={e.url}> <ItemPerson  showDetail={this.showDetail} person={e}/></ul>
            })
        }
    }
    showDetail = (key)=>{
        this.setState((state)=>{
            return{
                ...state,
                preload: true
            }
        });
        this.swapiServise.getPerson(key).then((res)=>{
            this.setState((state)=>{
                return{
                    ...state,
                    person: res,
                    preload: false
                }
            })
        });
        this.props.history.push(key)
    }
    renderPersonDetails = ()=>{
        if(this.state.person === null && this.state.preload === false){
            return(<div className={s.choose}>Choose item of list</div>)
        }else if(this.state.preload === true){
            return <Preloader/>
        }else if(this.state.person && this.state.preload === false){
            return <PersonDetailsRouter  person={this.state.person}/>
        }
    }
    render(){
        return(
            <div key="personDetail" className={`row jumbotron ${s.block}`}>
               <div className={`col-md-4 col-sm-12`}>{this.renderItemList()}</div>
               <div className={`col-md-6 offset-md-2 col-sm-12 ${s.render_person_details}`}>{this.renderPersonDetails()}</div>
            </div>
        )
    }
}
const PersonDetailsContainer = withRouter(PersonDetailsContainers)
export default PersonDetailsContainer;