import React from 'react';
import s from "./RandomPlanet.module.scss";
const RandomPlanet = (props)=>{
    let { id, planetName, population, rotation, diameter} = props.state;
    return(
        <div className={s.block}>
            <div> <img className={s.img} src={`https://starwars-visualguide.com/assets/img/planets/${id}.jpg`} alt="planet-icon"></img>  </div>
            <div>
                <h1 className="display-7">{planetName}</h1>
                <span className={`lead ${s.text}`}>Population {population}</span>
                <hr className={`my-4 ${s.hr}`}></hr>
                <span className={`lead ${s.text}`}>Rotation period {rotation}</span>
                <hr className={`my-4 ${s.hr}`}></hr>
                <span className={`lead ${s.text}`}>Diameter {diameter}</span>
            </div>
        </div>
    )
}
export default RandomPlanet;