import React from 'react';
import s from './Header.module.scss';
import {NavLink} from 'react-router-dom';
const Gamburger = (props)=>{
    let {hide}= props;
    return(
        <div className={s.overlay}>
            <div className={s.nav_gamburger} id="navbarColor02">
                <div onClick={hide} className={s.close}>&times;</div>
                    <ul className={s.gamburger_ul}>
                        <li className="nav-item">
                            <NavLink onClick={hide} className="nav-link" to="/people/">People </NavLink>
                        </li>
                        <li className="nav-item">
                            <NavLink onClick={hide} className="nav-link" to="/planets/">Planets</NavLink>
                        </li>
                        <li className="nav-item">
                            <NavLink onClick={hide} className="nav-link" to="/starships/">Starships</NavLink>
                        </li>
                    </ul>
            </div>
        </div>
    )
}
class Header extends React.Component{
    state={
        gamburger: false
    }
    show = ()=>{
        this.setState((state)=>{
            return {
                ...state,
                gamburger: true
            }
        })
    }
    hide= ()=>{
        this.setState((state)=>{
            return{
                ...state,
                gamburger: false
            }
        })
    }
    render(){
        if(this.state.gamburger){
            return <Gamburger hide={this.hide}/>
        }
        return(
            <div className={s.header}>
                <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
                    <NavLink className="navbar-brand" to="/">Star DB</NavLink>
                    <div className={s.nav} id="navbarColor02">
                        <ul className={`navbar-nav mr-auto ${s.ul}`}>
                            <li className="nav-item">
                                <NavLink className="nav-link" to="/people/">People </NavLink>
                            </li>
                            <li className="nav-item">
                                <NavLink className="nav-link" to="/planets/">Planets</NavLink>
                            </li>
                            <li className="nav-item">
                                <NavLink className="nav-link" to="/starships/">Starships</NavLink>
                            </li>
                        </ul>
                    </div>
                    <div onClick={()=> this.show()} className={s.gamburger}>
                        <span></span>
                        <span></span>
                        <span></span>
                    </div>
                    
                </nav>
                
            </div>
        )
    }
}
export default Header;