import React from 'react';
import s from './StarshipsDetails.module.scss';
import ItemStarships from './ItemStarships';
import SwapiService from '../../services/swapi-service';
import Preloader from '../Preloader/Preloader';
import Error from '../Error/Error';
import {withRouter} from 'react-router-dom';

const StarshipsDetails = (props)=>{
    let {id,starship:{name, length,passengers,model}} = props;
    return (
        <div >
            <div> <img className={s.img} alt="starships" src={`https://starwars-visualguide.com/assets/img/starships/${id}.jpg`}></img>  </div>
            <div>
                <h1 className="display-7">{name}</h1>
                <span className={`lead ${s.text}`}>Length {length} </span>
                <hr className={`my-4 ${s.hr}`}></hr>
                <span className={`lead ${s.text}`}>Model {model} </span>
                <hr className={`my-4 ${s.hr}`}></hr>
                <span className={`lead ${s.text}`}>Passengers {passengers} </span>
            </div>
        </div>
    )
}

class StarshipsDetailsContainers extends React.Component{
    swapiServise = new SwapiService();
    state = {
        allStarships: null,
        starship: null,
        id: null,
        preload: false
    }
    componentDidMount(){
        this.getAllStarships();

    }
    getAllStarships =()=>{
        this.swapiServise.getAllStarships().then((res)=>{
            this.setState((state)=>{
                return{
                    ...state,
                    allStarships: res.results
                }
            })
        })
        
    }
    renderItemList = ()=>{
        if(this.state.allStarships){
         return   this.state.allStarships.map((e)=>{
                         return <ul key={e.url}><ItemStarships showDetail={this.showDetail} starship={e}/></ul>
            })
        }
    }
    showDetail = (key)=>{
        this.setState((state)=>{
            return{
                ...state,
                preload: true
            }
        });
        this.props.history.push(key)
        this.swapiServise.getStarship(key).then((res)=>{
            this.setState((state)=>{
                return{
                    ...state,
                    starship: res,
                    preload: false
                }
            })
        }).catch(this.newError())
    }
    newError = ()=>{
        return <Error/>
    }
    renderStarshipsDetails = ()=>{
        if(this.state.starship === null && this.state.preload === false){
            return(<div className={s.choose}>Choose item of list</div>)
        }else if( this.state.preload === true){
            return <Preloader/>
        }else if(this.state.starship && this.state.preload === false){
            return <StarshipsDetails id={this.props.match.params.id} starship={this.state.starship}/>
        }
    }
    render(){
        return(
            <div className={`row jumbotron ${s.block}`}>
               <div className={`col-md-4 col-sm-12`}>{this.renderItemList()}</div>
               <div className={`col-md-6 offset-md-2 col-sm-12 ${s.starship} `}>{this.renderStarshipsDetails()}</div>
            </div>
        )
    }
}
let StarshipsDetailsContainer = withRouter(StarshipsDetailsContainers);
export default StarshipsDetailsContainer;