import React from 'react';
import s from './RandomPlanet.module.scss';
import SwapiService from '../../services/swapi-service';
import Preloader from '../Preloader/Preloader';
import RandomPlanet from './RandomPlanet';
import Error from '../Error/Error';
class  RandomPlanetContainer extends React.Component{
    state = {
        id: null,
        planetName: null,
        population: null,
        rotation: null,
        diameter: null,
        loading: true,
        error: false
    }
    componentDidMount(){
        this.getPlanet();
        this.repeatRandom();
    }
    repeatRandom= ()=>{
        setInterval(this.getPlanet, 5000)
    }
    componentWillUnmount(){
        clearInterval(this.repeatRandom)
    }
    getPlanet = ()=>{
        const swapiService = new SwapiService();
        let id = Math.floor(Math.random()*10+1);
        swapiService.getPlanet(id).then(({name, rotation_period, population, diameter})=>{
            this.setState((state)=>{
                return{
                    ...state,
                    planetName: name,
                    population,
                    rotation: rotation_period,
                    diameter,
                    id,
                    loading: false
                }
            })
        }).catch(this.onError)
    }
    onError = ()=>{
        this.setState((state)=>{
            return{
                ...state,
                error: true,
                loading: false
            }
        })
    }
    whatRender = ()=> {
        if(this.state.loading && this.state.error === false){
            return <Preloader/>
        }else if( this.state.loading === false && this.state.error){
            return <Error/>
        }else{
            return <RandomPlanet state={this.state}/>
        }
    }

   render(){
        return(
            <div className={`jumbotron ${s.random_block}`}>
                {this.whatRender()}
            </div>
        )
   }
}
export default RandomPlanetContainer;