import React from 'react';
import s from './StarshipsDetails.module.scss';
const ItemStarships= (props)=>{
       let {starship: {name,url}, showDetail} = props;
       let reg = /\/([0-9]*)\/$/;
       let key = url.match(reg)[1];
    return (
        <div>
            <ul className=" list-group">
                <li onClick={()=> showDetail(key)}  className={`list-group-item d-flex justify-content-between align-items-center ${s.item}`}>
                   <span> {name}</span>
                </li>
            </ul>
        </div>
    )
}
export default ItemStarships;