import React from 'react';
import {BrowserRouter, Route} from 'react-router-dom';
import s from './App.module.scss';
import Header from '../Header/Header';
import PersonDetailsContainer from '../PersonDetails/PersonDetailsContainer';
import PlanetDetailsContainer from '../PlanetDetails/PlanetDetailsContainer';
import StarshipsDetailsContainer from '../StarshipsDetails/StarshipsDetailsContainer';
import RandomPlanetContainer from '../RandomPlanet/RandomPlanetContainer';
import SwapiService from '../../services/swapi-service';
import Preloader from '../Preloader/Preloader';
class App extends React.Component{
    swapiServise = new SwapiService();
    state = {
        id:null,
        person: null,
        planet: null,
        preloader: false
    }
   
    renderDetail = (key)=>{
        this.setState((state)=>{
            return {
                ...state,
                preloader: true
            }
        });
        this.renderPersonDetails();
        this.swapiServise.getPerson(key)
            .then((res)=>{
                this.setState((state)=>{
                    return{
                        ...state,
                        id: key,
                        person: res,
                        preloader:false
                    }
                })
            })
        
    }
    renderDetailPlanet = (key)=>{
        this.setState((state)=>{
            return {
                ...state,
                preloader: true
            }
        });
        this.renderPersonDetails();
        this.swapiServise.getPlanet(key)
            .then((res)=>{
                this.setState((state)=>{
                    return{
                        ...state,
                        id: key,
                        planet: res,
                        preloader:false
                    }
                })
            })
        
    }
    renderPersonDetails = (newComponent)=>{
        if(this.state.preloader){
            return <div className={`col-md-7 offset-md-0 jumbotron ${s.person}`}> 
                    <Preloader/>
                 </div>
        }else if(!this.state.person){
            return(
                <div className={`col-md-7 offset-md-0 jumbotron ${s.person}`}> 
                    <span>Ð¡hoose a character</span>
                </div>
            );
        }else{
            return newComponent
        }
    }
    render(){
        return(
            <BrowserRouter>
                <div>
                    <Header/>
                    <RandomPlanetContainer/>
                    <Route path="/" render={()=> <h1>Welcome to starDB </h1>} exact/>
                    <div className={s.block}>
                        <Route path="/people/:id?" render={()=> <PersonDetailsContainer/>}/>
                    </div>
                    <div className={s.block}>
                         <Route path="/planets/:id?" render={()=>  <PlanetDetailsContainer/>}/>
                    </div>
                    <div className={s.block}>
                       <Route path="/starships/:id?" render={()=>  <StarshipsDetailsContainer/>}/>
                    </div>
                </div>
            </BrowserRouter>
        )
    }
}
export default App;