import React from 'react';
import s from './PlanetDetails.module.scss';
import ItemPlanet from './ItemPlanet';
import SwapiService from '../../services/swapi-service';
import Preloader from '../Preloader/Preloader';
import Error from '../Error/Error';
import {withRouter} from 'react-router-dom';

const PlanetDetails = (props)=>{
    let {id,planet:{name, rotation_period, diameter,population}} = props;
    return (
        <div >
            <div> <img className={s.img} alt="planet" src={`https://starwars-visualguide.com/assets/img/planets/${id}.jpg`}></img>  </div>
            <div>
                <h1 className="display-7">{name}</h1>
                <span className={`lead ${s.text}`}>Rotation period {rotation_period} </span>
                <hr className={`my-4 ${s.hr}`}></hr>
                <span className={`lead ${s.text}`}>Diameter {diameter} </span>
                <hr className={`my-4 ${s.hr}`}></hr>
                <span className={`lead ${s.text}`}>Population {population} </span>
            </div>
        </div>
    )
}

class PlanetDetailsContainers extends React.Component{
    swapiServise = new SwapiService();
    state = {
        allPlanets: null,
        planet: null,
        preload: false
    }
    componentDidMount(){
        this.getAllPlanets();

    }
    getAllPlanets =()=>{
        this.swapiServise.getAllPlanets().then((res)=>{
            this.setState((state)=>{
                return{
                    ...state,
                    allPlanets: res.results
                }
            })
        }).catch(this.newError())
    }
    newError = ()=>{
        return <Error/>
    }
    renderItemList = ()=>{
        if(this.state.allPlanets){
         return   this.state.allPlanets.map((e)=>{
                         return <ul key={e.url}><ItemPlanet showDetail={this.showDetail} planet={e}/></ul>
            })
        }
    }
    showDetail = (key)=>{
        this.setState((state)=>{
            return{
                ...state,
                preload: true
            }
        });
        this.props.history.push(key)
        this.swapiServise.getPlanet(key).then((res)=>{
            this.setState((state)=>{
                return{
                    ...state,
                    planet: res,
                    preload: false
                }
            })
        });
    }
    renderPlanetDetails = ()=>{
        if(this.state.planet === null && this.state.preload === false){
            return(<div className={s.choose}>Choose item of list</div>)
        }else if(this.state.preload === true){
            return <Preloader/>
        }else if(this.state.planet && this.state.preload === false){
            return <PlanetDetails id={this.props.match.params.id} planet={this.state.planet}/>
        }
    }
    render(){
        return(
            <div className={`row jumbotron ${s.block}`}>
               <div className={`col-md-4 col-sm-12 `}>{this.renderItemList()}</div>
               <div className={`col-md-6 offset-md-2 col-sm-12 ${s.render_planet_details}`}>{this.renderPlanetDetails()}</div>
            </div>
        )
    }
}
let PlanetDetailsContainer = withRouter(PlanetDetailsContainers);
export default PlanetDetailsContainer;